import React from 'react'
import ListView from './ListView'

export const DISPLAY = {
    WELCOME: 0,
    LIST_VIEW: 1,
    LIST_URGENT: 2
}

export default function MainDisplay({template, data}) {
    console.log('render MainDisplay')
    let content = null
    if (template === DISPLAY.WELCOME) {
        content = 'Welcome!'
    }
    else if (template === DISPLAY.LIST_VIEW) {
        content = <ListView list_state={data}/>
    }
    else if (template === DISPLAY.LIST_URGENT) {
        content = 'not used yet'
    }
    return (
        <main className="overflow-x-hidden overflow-y-auto flex-grow-1">
            {content}
        </main>
    )
}

