import React, { useState } from 'react'
import { xcContext } from './xcContext'
import Modal, { ModalContext } from './Modal'
import TaskForm from './TaskForm'
import Icon from './Icon'
import NotificationMaker from './NotificationMaker'
import MainDisplay, { DISPLAY } from './MainDisplay'
import { list_from_map, SORT_BY } from './ListView'

const Dashboard = ({xc, client}) => {
    console.log('render dash')
    const [modalContent, setModalContent] = useState(false)
    const modalContextValue = {content: modalContent, setContent: setModalContent}
    const show_Modal_with_TaskFormSimple = function() {
        const hide_Modal = () => {
            setModalContent(null)
        }
        const task_form_simple = <TaskForm handleCloseForm={hide_Modal}/>
        setModalContent(task_form_simple)
    }

    const [task_list, set_task_list] = useState(null)
    const [mainDisplayState, setMainDisplayState] = useState(null)

    const showListView = (filters=[], sort_fn) => {
        setMainDisplayState(DISPLAY.LIST_VIEW)
        set_task_list(null)
        function handleReply(results) {
            console.log('%cupdating list', "color: white; background-color: blue; padding: 5px;");

            let new_task_list = sort_fn(list_from_map(results))
            set_task_list(new_task_list)
        }
        xc.get_tasks({filters: filters, callback: handleReply})
    }
    
    const [activeTab, setActiveTab] = useState('All')
    function NavLink({clickCallback=null, i, t, highlight_after_click=true, dropdown=false}) {
        //console.log('render nav item')
        const handleClick = () => {
            if (highlight_after_click) {setActiveTab(t)}
            if (clickCallback) {clickCallback()}
        }
        const addClasses = (t===activeTab)? 'text-primary-emphasis' : 'link-secondary'
        return (
            <a onClick={handleClick} href='# '
            className={`nav-link ${addClasses} text-center text-decoration-none`}
            data-bs-toggle={dropdown? 'dropdown' : ''}
            >
                <div className='fs-3'><Icon i={i}/></div>
                <div style={{fontSize: 'small'}}>{t}</div>
            </a>
        )
    }

    const dropup = (
        <div className='nav-item dropup'>
            <NavLink dropdown={true} highlight_after_click={false} i='bi-sliders' t='Options'/>                
            <ul className="dropdown-menu">
                <li><NavLink clickCallback={xc.signOut} i='bi-box-arrow-left' t='Sign Out'/></li>
                <li><NotificationMaker port_to_sw={client.port_to_sw} /></li>
            </ul>
        </div>
    )

    return (
        <xcContext.Provider value={xc}>
        <ModalContext.Provider value={modalContextValue}>
            <Modal modalContent={modalContent}/>

            <MainDisplay template={mainDisplayState} data={task_list} />

            <nav className='nav nav-underline nav-justified bg-body-tertiary'>
                {dropup}
                <NavLink clickCallback={()=>{showListView([xc.filter.started], SORT_BY.PRIORITY)}} 
                    i='bi-list-task' t='All'/>
                <NavLink clickCallback={()=>{showListView([xc.filter.started], SORT_BY.DUE)}} 
                    i='bi-hourglass-bottom' t='List'/>
                <NavLink clickCallback={show_Modal_with_TaskFormSimple} i='bi-file-earmark-plus' t='New Task' highlight_after_click={false}/>
            </nav>
        </ModalContext.Provider>
        </xcContext.Provider>
    )
}

export default Dashboard;